<template>
    <div>
    <ul id="dropdown1" class="dropdown-content section">
            <li><router-link href="#Interests" :to="{ name: 'Gmod' }">Garrys Mod</router-link></li>
            <li><router-link href="#Interests" to="/fp-zarp">FP/ZARP</router-link></li>
            <li><router-link href="#Interests" to="/hammer">Mapping</router-link></li>
    </ul>
    <ul id="dropdown2" class="dropdown-content">
            <li><router-link to="/myprofile">My Profile</router-link></li>
            <li><router-link to="/">My Connections</router-link></li>
            <li><router-link  to="/search">Search</router-link></li>
            <li><router-link to="/creategroup">Create Group</router-link></li>
            <li class="divider"></li>
            <li><a >Logout</a></li>
    </ul>
   <ul id="nav-mobile section " class="right menu-padding">
        <li><router-link to="/" href="#About">About</router-link></li>
        <li><a class="dropdown-button" href="#" data-activates="dropdown1">Interests<i class="material-icons right">arrow_drop_down</i></a></li>
        <li><router-link to="#Showcase" href="#Showcase">Showcase</router-link></li>
    </ul>
    </div>
</template>

<<script>
    export default {
        name: 'submenus',
        mounted(){
                $(".dropdown-button").dropdown();
                $('.scrollspy').scrollSpy();
                this.$nextTick(()=>{
                console.log("Next tick")
                setTimeout(()=>{
                    $(".dropdown-button").dropdown();
                    $('.scrollspy').scrollSpy();
                }, 1000)
                
            })
        },
        activated(){
            console.log("activated")
            this.$nextTick(()=>{
                console.log("Next tick")
                setTimeout(()=>{
                    $(".dropdown-button").dropdown();
                    $('.scrollspy').scrollSpy();
                }, 1000)
                
            })
        }
    }
</script>

<style scoped>
</style>


